import { createWebHistory, createRouter, RouteRecordRaw } from "vue-router";
import { nextTick } from "vue";
import Home from "@/views/Home.vue";
import Login from "@/views/Login.vue";
import Profile from "@/views/Profile.vue";
import PockerRoom from "@/views/PockerRoom.vue"
// lazy-loaded
// const Profile = () => import("./components/Profile.vue")
// const BoardAdmin = () => import("./components/BoardAdmin.vue")
// const BoardModerator = () => import("./components/BoardModerator.vue")
// const BoardUser = () => import("./components/BoardUser.vue")

const DEFAULT_TITLE = 'Loading';


const routes: Array<RouteRecordRaw> = [
    {
        path: "/vue-page/",
        name: "Home",
        component: Home,
    },
    {
        path: "/vue-page/home",
        redirect: '/'
    },
    {
        path: "/vue-page/login",
        name: "Login",
        component: Login,
    },
    {
        path: "/vue-page/register",
        name: "Register",
        component: () => import("@/views/Register.vue"),
    },
    {
        path: "/vue-page/profile",
        name: "profile",
        // lazy-loaded
        component: Profile,
    },
    {
        path: "/vue-page/pocker",
        name: "Pocker",
        component: () => import('@/views/Pocker.vue')
    },
    {
        path: '/vue-page/pocker/:id',
        name: 'PockerRoom',
        component: PockerRoom,
        beforeEnter: (to, _, next) => {
            const { id } = to.params
            if (Array.isArray(id)) {
                next({ path: '/vue-page/error' })
                return
            }

            //   const index = parseInt(id)
            //   if (index < 0 || index >= facts.length) {
            //     next({ path: '/error' })
            //     return
            //   }

            next()
        }
    },
    // {
    //     path: "/admin",
    //     name: "admin",
    //     // lazy-loaded
    //     component: BoardAdmin,
    // },
    // {
    //     path: "/mod",
    //     name: "moderator",
    //     // lazy-loaded
    //     component: BoardModerator,
    // },
    // {
    //     path: "/user",
    //     name: "user",
    //     // lazy-loaded
    //     component: BoardUser,
    // },
    {
        path: '/:catchAll(.*)',
        name: 'PageNotFound',
        component: () => import('@/views/PageNotFound.vue')
    }
];


const router = createRouter({
    history: createWebHistory(),
    routes,
});

router.beforeEach((to, from, next) => {
    // const publicPages = ['/login', '/', '/register', '/pocker'];
    // const authRequired = !publicPages.includes(to.path);

    const privitePage = ['/profile'];
    const authRequired = privitePage.includes(to.path);
    const loggedIn = localStorage.getItem('user');
    // trying to access a restricted page + not logged in
    // redirect to login page
    if (authRequired && !loggedIn) {
        next('/login');
    } else {
        next();
    }
});

router.afterEach((to, from) => {
    nextTick(() => {
        document.title = `${String(to.name)}` || DEFAULT_TITLE
    })
})

export default router;
